.slideShow {
  
}
.cbSlideshow {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0px;
  left: 0px;
  z-index: 0;
  list-style-type: none;
  margin: 0;
  opacity: 0.8;
}

.cbSlideshow li span {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0px;
  left: 0px;
  color: transparent;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  opacity: 0;
  z-index: 0;
  -webkit-animation: imageAnimation 36s linear infinite 0s;
          animation: imageAnimation 36s linear infinite 0s;
}

@-webkit-keyframes imageAnimation {
  0% {
    opacity: 0;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  8% {
    opacity: 1;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  17% {
    opacity: 1
  }
  25% {
    opacity: 0
  }
  100% {
    opacity: 0
  }
}

@keyframes imageAnimation {
  0% {
    opacity: 0;
    -webkit-animation-timing-function: ease-in;
            animation-timing-function: ease-in;
  }
  8% {
    opacity: 1;
    -webkit-animation-timing-function: ease-out;
            animation-timing-function: ease-out;
  }
  17% {
    opacity: 1
  }
  25% {
    opacity: 0
  }
  100% {
    opacity: 0
  }
}

.cbSlideshow li:nth-child(1) span {
  background-image: url('imgs/bg-1.jpg');
}

.cbSlideshow li:nth-child(2) span {
  background-image: url('imgs/bg-2.jpg');
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
}

.cbSlideshow li:nth-child(3) span {
  background-image: url('imgs/bg-3.jpg');
  -webkit-animation-delay: 12s;
          animation-delay: 12s;
}

.cbSlideshow li:nth-child(4) span {
  background-image: url('imgs/bg-4.jpg');
  -webkit-animation-delay: 18s;
          animation-delay: 18s;
}

.cbSlideshow li:nth-child(5) span {
  background-image: url('imgs/bg-5.jpg');
  -webkit-animation-delay: 24s;
          animation-delay: 24s;
}

.cbSlideshow li:nth-child(6) span {
  background-image: url('imgs/bg-6.jpg');
  -webkit-animation-delay: 32s;
          animation-delay: 32s;
}


